//https://css-tricks.com/hexagons-and-beyond-flexible-responsive-grid-patterns-sans-media-queries/
HTML, BODY {
  margin: 0;
  padding: 0;
}

.gallerycont{
  padding-top: 2.5vw;
}

.hidden{
  position: absolute; 
  left: -999em;
}


.wrapper {
  position: relative;
  margin: auto;
  padding: 0.25vw 5vw 0.25vw 5vw ;
  max-width: 90%;
  max-height: 100%;
  flex-grow: 1;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 1vmin;
  justify-items: center;
  align-items: center;
  

}

.fecha{
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 500;
  color: rgb(12, 7, 26);
}

.imagenes{
  z-index: 1;
  grid-column: span 2;
  max-width: 20vw;
  max-height: 20vw;
  //ESTOS SON LOS QUE CONTROLAN EL TAMAÑO, EL "GRAB". DEPENDE DE ESTOS PARA REGULAR EL TAMAÑO DE LOS CUADROS


  margin-bottom: -52%;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  transform: scale(1);
  transition: all .25s;
  
  &:nth-child(7n +1) {
    grid-column: 2 / span 2;
  }
  
  &:hover {
    z-index: 2;
    transform: scale(2);

  }
}
IMG{
  overflow: hidden;
  max-width: 20VW;
}



