HTML, BODY {
  margin: 0;
  padding: 0;
}

.gallerycont {
  padding-top: 2.5vw;
}

.hidden {
  position: absolute;
  left: -999em;
}

.wrapper {
  max-width: 90%;
  max-height: 100%;
  grid-gap: 1vmin;
  flex-grow: 1;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(8, 1fr);
  place-items: center;
  margin: auto;
  padding: .25vw 5vw;
  display: grid;
  position: relative;
}

.fecha {
  color: #0c071a;
  margin: 0;
  padding: 0;
  font-family: Josefin Sans, sans-serif;
  font-weight: 500;
}

.imagenes {
  z-index: 1;
  max-width: 20vw;
  max-height: 20vw;
  clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  grid-column: span 2;
  margin-bottom: -52%;
  transition: all .25s;
  transform: scale(1);
}

.imagenes:nth-child(7n+1) {
  grid-column: 2 / span 2;
}

.imagenes:hover {
  z-index: 2;
  transform: scale(2);
}

IMG {
  max-width: 20vw;
  overflow: hidden;
}

